<template>
  <div id="app">
    <h1>TodoList</h1>
    <addNew @submitNewItem = "addNewItem"></addNew>
    <h5>toDone</h5>
    <thelist @handleJudge = "toDone"  v-bind:List = "todoList"></thelist>
    <h5>toDelete</h5>
    <thelist @handleDelete = "toDelete" v-bind:List = "doneList" :ListType = "true"></thelist>
    
  </div>
</template>
<style scoped>
h1{
  text-align: center;
}
h5{
  text-align: center;
}
</style>
<script>
import addNew from './components/addNew';
import thelist from './components/theList';

export default {
  name: 'app',
  data(){
    return {
      todoList:[],
      doneList:[]
    }
  },
  methods:{
    addNewItem: function(newItem){
      this.todoList.push(newItem);
    },
    toDone: function(index){
      this.doneList.push(this.todoList.splice(index,1)[0]);
    },
    toDelete: function(index){
      this.doneList.splice(index,1);
    }
  },
  components: {
    addNew,
    thelist
  }
}
</script>


