<template>
    <div>
        <div class="input-group mb-3">
            <el-input v-model="newItem" class="input1" placeholder="请输入待办事项" type="text" @keyup.enter.native='handleAdd'></el-input>
              
            </div>
    </div>
</template>
<style scoped>
.input1{
    width: 40%;
    margin-left: 30%;
}
</style>
<script>    
export default {
    name:'addNew',
    data(){
        return {
            newItem:""
        }
    },
    methods:{
        handleAdd:function(){
            if(this.newItem == ""){
                alert("不能为空");
                return;
            }
            this.$emit('submitNewItem',this.newItem);
            this.newItem = "";
        }
    }
}
</script>


